.Item {
  box-shadow: 5px 5px 10px lightgray;
  padding: 40px 20px;
  position: relative;
  border-radius: 10px;
  transition: 0.5s ease all;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 9px;
}
.Item:hover {
  margin-top: 10px;
  border-bottom: 10px solid black;
}
.price {
  position: absolute;
  top: 0;
  left: 0;
  width: 80px;
  background: blue;
  font-size: 1.2rem;
  color: white;
  border-top-left-radius: 10px;
  text-align: center;
}

.textSection p.title {
  margin: 0 10px;
  font-weight: bold;
  color: darkblue;
}

.textSection p.decs {
  font-size: 0.9rem;
}

.imageSection {
  flex: 1;
}
.imageSection img {
  width: 128px;
  height: 128px;
  border-radius: 5px;
}
