.topBar {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.topBar div {
  cursor: pointer;
  font-size: 1.5rem;
  color: #535e79;
}
.topBar div.center {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

h1 {
  text-align: center;
}
.cats {
  display: flex;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
  font-size: 1.4rem;
  color: darkCyan;
}
.cat {
  text-align: center;
}
a {
  text-decoration: none;
  color: darkCyan;
}
a:visited,
a:active {
  color: darkcyan;
}
.cat img {
  width: 128px;
  height: 128px;
  border-radius: 10px;
  box-shadow: 5px 5px 10px darkgray;
}
