.FixedButton {
  position: fixed;
  bottom: 20px;
  width: 70%;
}

.FixedButton button {
  width: 100%;
  border: none;
  outline: none;
  background-color: #535e79;
  padding: 20px 0;
  color: white;
  font-size: 1.3rem;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 1px 5px 10px #222;
}
